<script setup lang="ts">
import { computed } from 'vue';

const props = defineProps<{
  status: string;
  type?: 'instance' | 'node';
}>();

const config = computed(() => {
  const s = props.status.toLowerCase();
  
  if (props.type === 'node') {
    switch (s) {
      case 'connected': return { color: 'bg-green-100 text-green-700', text: '' };
      case 'disconnected': return { color: 'bg-gray-100 text-gray-700', text: '' };
      case 'error': return { color: 'bg-red-100 text-red-700', text: '' };
      default: return { color: 'bg-gray-100 text-gray-700', text: s };
    }
  }

  // Instance status
  switch (s) {
    case 'running': return { color: 'bg-green-100 text-green-700', text: '' };
    case 'stopped': return { color: 'bg-gray-100 text-gray-700', text: '' };
    case 'error': return { color: 'bg-red-100 text-red-700', text: '' };
    case 'restarting': return { color: 'bg-blue-100 text-blue-700', text: '' };
    default: return { color: 'bg-gray-100 text-gray-700', text: s };
  }
});
</script>

<template>
  <span 
    class="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium whitespace-nowrap flex-shrink-0"
    :class="config.color"
  >
    {{ config.text }}
  </span>
</template>
